makeCanvas (context) {
const _handleStr = (str) => {
if (str.length > 10) {
return (str.substr(0, 10) + '...')
} else {
return str
}
}
const nickname = this.myInfo.nickname
context.drawImage(this.imgUpload, 0, 0, 691, 998);
context.font="32px Arial";
context.fillStyle = '#CD2E2E';
context.fillText(`我是${_handleStr(nickname)},快来和+平台抽奖吧!`,34,393);
